document.addEventListener('DOMContentLoaded', () => {
  const button = document.querySelector('.hamburger-menu')
  const menu = document.querySelector('.mobile-navigation')
  const list = [...document.querySelectorAll('.dropdown-list')]
  const items = [...document.querySelectorAll('.dropdown-list__label')]

  button.addEventListener('click', (e) => {
    e.preventDefault()
    menu.classList.add('active')
  })

  menu.addEventListener('click', (e) => {
    const target = e.target
    const check = target.classList.contains('mobile-navigation')

    if (check) {
      e.preventDefault()
      target.classList.remove('active')
    }
  })

  list.forEach(element => {
    element.addEventListener('click', (e) => {
      const target = e.target
      const check = target.classList.contains('dropdown-list__label') && !target.classList.contains('dropdown-list__label_link')

      if (check) {
        e.preventDefault()
        items.filter(el => el !== target).forEach(el => el.classList.remove('active'))
        target.classList.toggle('active')
      }
    })
  })
})
