import Vue from 'vue'
import Cart from './cart/Cart.vue'
import CategoryButton from './categoryButton/CategoryButton.vue'
import Location from './location/Location.vue'
import SeoBlock from './seoBlock/SeoBlock.vue'
import SeoBlockTop from './seoBlockTop/SeoBlockTop.vue'
import FastOrder from './fastOrder/FastOrder.vue'
import CallbackModal from './callbackModal/CallbackModal.vue'
import store from './store'

Vue.config.productionTip = false

if (document.querySelector('location-modal')) {
  new Vue({
    store,
    components: {
      'location-modal': Location
    }
  }).$mount('location-modal')
}

if (document.querySelector('product-cart')) {
  new Vue({
    store,
    components: {
      'product-cart': Cart
    }
  }).$mount('product-cart')
}

if (document.querySelector('callback-modal')) {
  new Vue({
    store,
    components: {
      'callback-modal': CallbackModal
    }
  }).$mount('callback-modal')
}

const buttons = [...document.querySelectorAll('.category-button, .product-form__to-cart, .category-list__item-button')]

buttons.forEach(element => {
  const dataSet = element.dataset.add2basket.split(',')
  const data = dataSet.map(item => item.trim())
  new Vue({
    store,
    render: h => h(CategoryButton, {
      props: {
        dataArray: data
      }
    })
  }).$mount(`#${element.id}`)
})

const seo = [...document.querySelectorAll('seo-block')]

seo.forEach(element => {
  new Vue({
    store,
    components: {
      'seo-block': SeoBlock
    }
  }).$mount(`#${element.id}`)
})

const seoTop = [...document.querySelectorAll('seo-block-top')]

seoTop.forEach(element => {
  new Vue({
    store,
    components: {
      'seo-block-top': SeoBlockTop
    }
  }).$mount(`#${element.id}`)
})

const fastOrder = [...document.querySelectorAll('fast-order')]

fastOrder.forEach(element => {
  new Vue({
    store,
    components: {
      'fast-order': FastOrder
    }
  }).$mount(`#${element.id}`)
})
