const categoryPreview = [...document.querySelectorAll('.category-preview')]

if (categoryPreview) {
  document.addEventListener('DOMContentLoaded', () => {
    categoryPreview.forEach(element => {
      const tabs = [...element.querySelectorAll('.category-tabs__item')]
      const panels = [...element.querySelectorAll('.category-view')]
      element.addEventListener('click', (e) => {
        e.preventDefault()
        const target = e.target
        const targetParent = target.parentElement
        const check = targetParent.classList.contains('category-tabs__item')
        const anchor = targetParent.getAttribute('href').replace('#', '')

        if (check) {
          tabs.forEach(element => {
            element.classList.remove('category-tabs__item_active')
          })

          panels.forEach(element => {
            element.classList.remove('category-view_visible')
          })

          targetParent.classList.add('category-tabs__item_active')
          document.getElementById(anchor).classList.add('category-view_visible')
        }
      })
    })
  })
}
