<template>
  <button
    @click.prevent="addToCart"
    class="category-list__item-button"
  >
    в корзину
  </button>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'CategoryButton',
  props: {
    dataArray: {
      type: Array,
      required: true,
      default: function () {
        return []
      }
    }
  },
  methods: {
    ...mapActions(['addCart']),
    addToCart () {
      let priceVal
      const inputs = [...document.querySelectorAll('.price-value__item input')]

      if (inputs.length) {
        priceVal = inputs.find(item => item.checked).value
      }
      const [product, price, city] = this.dataArray
      this.addCart({
        product_id: product,
        price_id: priceVal || price,
        city_id: city
      }).then((res) => {
        const modal = document.getElementById('add-cart')
        const backdrop = document.querySelector('.backdrop')
        const modalContent = modal.querySelector('.add-item')
        let close
        modalContent.innerHTML = res
        backdrop.classList.add('backdrop_opened')
        modal.classList.add('modal-wrapper_opened')
        close = modalContent.querySelector('.add-item__button_grey')
        close.addEventListener('click', () => {
          backdrop.classList.remove('backdrop_opened')
          modal.classList.remove('modal-wrapper_opened')
        })
      })
    }
  }
}
</script>

<style lang="scss">
  .category-list__item-button {
    padding: 8.5px 18.5px;
    border: 1px solid #8fb35b;
    background: none;
    border-radius: 3px;
    color: #515151;
    font-size: 14px;
    cursor: pointer;

    &:hover {
      color: #fff;
      background-color: #8fb35b;
    }

    @media (max-width: 767px) {
      padding: 5.5px 18.5px;
      margin: auto;
      font-size: 11px;
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      padding: 6px 17px;
      flex-shrink: 0;
    }
  }

  .product-form__buttons {
    .category-list__item-button {
      padding: 12.5px 26px;
      margin: 0;
      color: #fff;
      background-color: #8fb35b;

      &:hover {
          background-color: #fbc000;
      }

      @media (max-width: 767px) {
          font-size: 14px;
      }
    }
  }
</style>
