document.addEventListener('DOMContentLoaded', () => {
  const price = [...document.querySelectorAll('.price-value__item input')]
  const description = [...document.querySelectorAll('.switch-description')]
  const tokens = [...document.querySelectorAll('.icon-token')]

  price.forEach(element => {
    element.addEventListener('change', (e) => {
      e.preventDefault()
      const target = e.target
      const vegas = target.getAttribute('vegas_token_count')
      const targetDescription = document.getElementById(target.value.toString())
      if (vegas) {
        tokens.forEach(token => {
          token.textContent = vegas
        })
      }
      description.forEach(element => {
        element.classList.remove('switch-description_active')
      })
      targetDescription.classList.add('switch-description_active')
    })
  })
})
