import Glide from '@glidejs/glide'

document.addEventListener('DOMContentLoaded', () => {
  const carousel = [...document.querySelectorAll('.main-carousel')]
  const category = [...document.querySelectorAll('.category-list')]
  const banners = [...document.querySelectorAll('.banner')]
  const products = [...document.querySelectorAll('.product__image')]

  carousel.forEach(element => {
    new Glide(element, {
      type: 'carousel',
      startAt: 0,
      perView: 1,
      gap: 0,
      autoplay: 7000,
      animationTimingFunc: 'ease',
      hoverpause: true,
      animationDuration: 1500
    }).mount()
  })

  category.forEach(element => {
    const glide = new Glide(element, {
      type: 'carousel',
      startAt: 0,
      perView: 3,
      gap: 40,
      autoplay: 7000,
      animationTimingFunc: 'ease',
      hoverpause: true,
      animationDuration: 1500,
      breakpoints: {
        767: {
          perView: 2,
          gap: 9
        },
        1024: {
          gap: 16
        }
      }
    }).mount()

    if (window.matchMedia('(max-width: 767px)').matches) {
      glide.destroy()
    }
  })

  banners.forEach(element => {
    new Glide(element, {
      type: 'carousel',
      startAt: 0,
      perView: 1,
      gap: 0,
      autoplay: 7000,
      animationTimingFunc: 'ease',
      hoverpause: true,
      animationDuration: 1500
    }).mount()
  })

  products.forEach(element => {
    new Glide(element, {
      type: 'carousel',
      startAt: 0,
      perView: 1,
      gap: 0,
      autoplay: 7000,
      animationTimingFunc: 'ease',
      hoverpause: true,
      animationDuration: 1500
    }).mount()
  })
})
