<template>
  <transition name="fade">
    <div class="seo-block" :class="{ 'seo-block_open': expand }" :style="elStyle">
        <slot/>
      <a
        href="#"
        class="seo-block__expand"
        @click.prevent="setHeight"
      >{{ expand ? 'Скрыть текст' : 'Читать полностью' }}</a>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'SeoBlock',
  data () {
    return {
      expand: false,
      elHeight: 0
    }
  },
  computed: {
    elStyle() {
      const style = this.expand ? { height: this.elHeight } : {}
      return style
    }
  },
  methods: {
    setHeight() {
      const getValue = (el, property) => {
        return parseInt(window.getComputedStyle(el).getPropertyValue(property))
      }
      const elements = this.$slots.default.filter(item => item.elm.nodeType !== 3)
      const heightArr = elements.map(item => getValue(item.elm, 'margin-top') + getValue(item.elm, 'margin-bottom') + item.elm.offsetHeight)
      this.elHeight = `${heightArr.reduce((accumulator, currentValue) => accumulator + currentValue + 10)}px`
      this.expand = !this.expand
    }
  }
}
</script>

<style lang="scss">
  .seo-block {
    position: relative;
    margin-bottom: 33px;
    height: 55px;
    overflow: hidden;
    border-top: 1px solid #d6d6d6;
    border-bottom: 1px solid #d6d6d6;
    transition: all ease 0.3s;

    &_open {
      height: auto;
    }

    &__list {
      padding-left: 28px;
      margin-top: 10px;
      margin-bottom: 7px;
      list-style: none;

      &-item {
        position: relative;
        margin-bottom: 8px;
        color: #515151;
        font-size: 12px;

        &:last-child {
          margin-bottom: 0;
        }

        &::before {
          content: '';
          position: absolute;
          top: calc(50% - 4px);
          left: -20px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: #8fb35b;
        }
      }
    }

    &__expand {
      position: absolute;
      bottom: 14px;
      right: 35px;
      color: #7e9d51;
      font-size: 12px;
      text-decoration: underline;
    }

    @media (max-width: 767px) {
      height: 68px;
      margin-bottom: 0;

      &__expand {
        bottom: 7px;
        right: 15px;
      }
    }

    @media (min-width: 768px) and (max-width: 1024px) {
      margin-bottom: 0;
    }
  }

    .fade-enter-active {
        transition: all ease 1s;
    }

    .fade-leave-active {
        transition: all ease 0.5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
        opacity: 0;
    }
</style>
