const modalOpen = (content) => {
  const body = document.body
  const backdrop = document.querySelector('.backdrop')
  const modalWrapper = document.createElement('div')
  const modal = document.createElement('div')
  const close = document.createElement('i')
  modalWrapper.classList.add('modal-wrapper')
  close.classList.add('icon-close')
  modal.classList.add('review-item__modal')
  modal.innerHTML = content
  modal.appendChild(close)
  modalWrapper.appendChild(modal)
  body.appendChild(modalWrapper)
  backdrop.classList.add('backdrop_opened')
  modalWrapper.classList.add('modal-wrapper_opened')

  close.addEventListener('click', () => {
    backdrop.classList.remove('backdrop_opened')
    modalWrapper.remove('modal-wrapper_opened')
  })
}

const setOverflow = (elementClass, value) => {
  const overflowElements = [...document.querySelectorAll(elementClass)]
  const openLink = document.createElement('a')
  openLink.classList.add('crop-text__button')
  openLink.innerText = 'Развернуть текст'

  overflowElements.forEach(element => {
    element.classList.add('crop-text')
    const textStyle = window.getComputedStyle(element, null)
    const lineHeight = Math.round(Number(textStyle.getPropertyValue('line-height').replace('px', '')))
    const textHeight = Math.round(Number(textStyle.getPropertyValue('height').replace('px', '')))
    const wordArray = element.innerHTML.split(' ')
    const text = element.textContent
    const cropText = document.createElement('span')
    const reviewContent = element.parentElement.innerHTML
    let crop

    if (textHeight / lineHeight >= value) {
      element.style.height = `${lineHeight * value}px`
      element.style.overflow = 'hidden'

      do {
        wordArray.pop()
        element.innerHTML = wordArray.join(' ')
        crop = text.replace(element.textContent, '')
        element.appendChild(openLink)
      } while (element.scrollHeight > element.offsetHeight)

      cropText.textContent = crop
      element.appendChild(cropText)

      openLink.addEventListener('click', (e) => {
        e.preventDefault()
        const target = e.target
        target.classList.toggle('open')
        modalOpen(reviewContent)
      })
    }
  })
}

window.addEventListener('load', () => {
  setOverflow('.review-item__text', 3)
})
