import cart from '../../api/cart'

const state = {
  dataArray: [],
  name: '',
  vendor: '',
  phone: '',
  done: false,
  orderId: '',
  userName: '',
  userPhone: ''
}

const getters = {}

const mutations = {
  setArray (state, payload) {
    state.dataArray = payload
  },
  setItemName (state, payload) {
    state.name = payload.name
  },
  setVendor (state, payload) {
    state.vendor = payload.article
  },
  setPhone (state, payload) {
    state.phone = payload.phone
  },
  checkDone (state, payload) {
    state.done = payload
  },
  setId (state, payload) {
    state.orderId = payload.order_id
  },
  setUserName (state, payload) {
    state.userName = payload
  },
  setUserPhone (state, payload) {
    state.userPhone = payload
  }
}

const actions = {
  setFastOrder ({ commit }, indexArrary) {
    const [product, city, price] = indexArrary
    commit('setArray', indexArrary)
    commit('checkDone', false)

    cart.getFastOrder((data) => {
      commit('setItemName', data)
      commit('setVendor', data)
      commit('setPhone', data)
    }, {
      product_id: product,
      price_id: price,
      city_id: city,
      nohtml: '1'
    })
  },
  fastOrder ({ commit, state }) {
    const [product, city, price] = state.dataArray
    cart.fastOrder((data) => {
      commit('setId', data)
      commit('checkDone', true)
    }, {
      product_id: product,
      price_id: price,
      city_id: city,
      fast_order_name: state.userName,
      fast_order_phone: state.userPhone,
      nohtml: '1'
    })
  },
  userName ({ commit }, name) {
    commit('setUserName', name)
  },
  userPhone ({ commit }, phone) {
    commit('setUserPhone', phone)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
