import axios from 'axios'
import getCookie from '../utils/getCookie'

const url = window.spa_url

const cityId = getCookie('CatalogCityId') || getCookie('CatalogCityIdRR')

const http = axios.create({
  baseURL: url,
  withCredentials: true,
  headers: { 'X-Catalog-Region': cityId }
})

export default {
  async getCart (callback) {
    try {
      const response = await http.get(`${url}/cart`)
      callback(response.data.data.cart)
    } catch (error) {
      console.log(error)
    }
  },
  async addToCart (callback, data) {
    try {
      const response = await http.post(`${url}/cart/add`, data)
      callback(response.data.data.cart, response.data.html)
    } catch (error) {
      console.log(error)
    }
  },
  async getFastOrder (callback, data) {
    try {
      const response = await http.post(`${window.location.origin}/api/basket/fast-order`, data)
      callback(response.data.data)
    } catch (error) {
      console.log(error)
    }
  },
  async fastOrder (callback, data) {
    try {
      const response = await http.post(`${window.location.origin}/api/basket/fast-order-confirm`, data)
      callback(response.data.data)
    } catch (error) {
      console.log(error)
    }
  }
}
