const headerTop = document.querySelector('.header__top')
const headerMiddle = document.querySelector('.header__middle')

document.addEventListener('DOMContentLoaded', () => {
  window.addEventListener('scroll', (e) => {
    const limit = headerTop.offsetHeight + headerMiddle.offsetHeight
    const windowScroll = window.scrollY

    if (windowScroll >= limit && !window.matchMedia('(max-width: 768px)').matches) {
      headerTop.classList.add('header__top_scroll')
    } else {
      headerTop.classList.remove('header__top_scroll')
    }
  })
})
