<template>
  <form
    @click="close"
    class="city-modal"
  >
    <span
      v-if="!mobile"
      class="city-modal__mobile-heading"
    >
      <span
        @click.prevent="$emit('close-modal')"
        class="fas fa-chevron-left"
      /> Выбор города доставки
    </span>
    <h2
      v-if="mobile"
      class="city-modal__heading"
    >
      Ваш город доставки
    </h2>
    <field
      v-if="mobile"
      ref="locationCountries"
      :list="countries"
      placeholder="Введите страну"
    />
    <field
      ref="locationCities"
      :list="cities"
      placeholder="Введите населённый пункт"
    />
    <button
      v-if="mobile"
      @click.prevent="$emit('close-modal')"
      class="city-modal__submit"
    >
      Да, верно
    </button>
    <button
      v-else
      @click.prevent="$emit('close-modal')"
      class="city-modal__submit"
    >
      Готово
    </button>
    <i
      v-if="mobile"
      @click.prevent="$emit('close-modal')"
      class="icon-close"
    />
  </form>
</template>

<script>
import Field from './Field.vue'
export default {
  name: 'LocationForm',
  props: {
    mobile: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  components: {
    Field
  },
  data () {
    return {
      countries: [
        'Австралия',
        'Венгрия',
        'Германия',
        'Италия',
        'Китай',
        'Литва',
        'Польша',
        'Россия',
        'США',
        'Чехия'
      ],
      cities: [
        'Куйбышев',
        'Купино',
        'Курильск',
        'Москва',
        'Волгоград',
        'Екатеринбург',
        'Уфа',
        'Норильск',
        'Красноярск',
        'Пенза'
      ]
    }
  },
  methods: {
    close (e) {
      const target = e.target
      if (target.classList.contains('city-modal')) {
        this.$refs.locationCountries.open = false
        this.$refs.locationCities.open = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.city-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 533px;
  padding: 47px 0 39px 0;

  &__heading {
    margin-top: 0;
    margin-bottom: 23px;
    text-align: center;
    color: #515151;
    font-size: 30px;
    font-weight: 400;
  }

  &__submit {
    margin-top: 32px;
    padding: 10px 50px;
    border-radius: 3px;
    border: none;
    outline: none;
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    background-color: #8fb35b;
    cursor: pointer;
  }

  &__mobile-heading {
    position: relative;
    display: block;
    width: 100%;
    padding: 14px 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.22);
    background-color: #8fb35b;
    color: #fff;
    font-size: 14px;
    text-align: center;

    .fas {
      position: absolute;
      left: 19px;
      top: calc(50% - 13px/2);
      font-size: 13px;
    }
  }

  @media (max-width: 767px) {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 0;
    padding-bottom: 10px;

    &__submit {
      margin-top: 0;
    }
  }
}
</style>
