<template>
  <span
    @click="setFastOrder(dataArray)"
    class="fast-order"
  >
    <slot />
  </span>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'FastOrder',
  props: {
    dataFastOrder: {
      type: String,
      required: true,
      default: ''
    }
  },
  computed: {
    dataArray () {
      const dataSet = this.dataFastOrder.split(',')
      const data = dataSet.map(item => item.trim())
      return [...data, '-1']
    }
  },
  methods: {
    ...mapActions(['setFastOrder'])
  }
}
</script>

<style lang="scss" scoped>
.fast-order {
  position: absolute;
  top: 0;
  right: 0;
  padding: 4px 15px;
  border-radius: 3px;
  box-shadow: 1px 1px 27px rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  z-index: -10;
  background-color: #8fb35b;
  border: none;
  outline: none;
  color: #fff;
  font-size: 14px;
  line-height: 21px;
  cursor: pointer;
  transition: all ease 0.3s;

  &:hover {
    background-color: #fbc000;
  }

  @media (max-width: 767px) {
    display: none;
  }
}
</style>
