import cart from '../../api/cart'

const state = {
  cart: null
}

const getters = {
  getCount: (state) => {
    const check = state.cart
    let count = 0
    let quantityArray

    if (check && check.items.length) {
      quantityArray = state.cart.items.map(item => item.quantity)
      count = quantityArray.reduce((accumulator, currentValue) => accumulator + currentValue)
    }

    return count
  }
}

const mutations = {
  setCart (state, payload) {
    state.cart = payload
  }
}

const actions = {
  setCart ({ commit }) {
    cart.getCart(payload => {
      commit('setCart', payload)
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
