<template>
  <form class="callback">
    <template v-if="done">
      Спасибо! Ваш заказ №{{ orderId }} принят. Мы обязательно перезвоним вам через 2 — 3 минуты.
    </template>
    <template v-else>
      <h2 class="callback__heading">
        {{ name }}
      </h2>
      <span class="callback__vendor-code">Артикул: {{ vendor }}</span>
      <p class="callback__description">
        Укажите свое имя и телефон.
        Мы перезвоним вам через 2 — 3 минуты.
      </p>
      <div class="callback__field">
        <input
          @input="userName($event.target.value)"
          type="text"
          placeholder="Имя"
          required
        >
      </div>
      <div class="callback__field callback__field_phone">
        <input
          @input="userPhone($event.target.value)"
          type="tel"
          pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
          :placeholder="phone ? phone : '+7 (495) 123-45-67'"
          required
        >
      </div>
      <button @click="fastOrder" class="callback__button">
        Отправить
      </button>
    </template>
  </form>
</template>

<script>
import { mapState, mapActions  } from 'vuex';

export default {
  name: 'CallbackModal',
  computed: {
    ...mapState({
      name: state => state.fastOrder.name,
      vendor: state => state.fastOrder.vendor,
      phone: state => state.fastOrder.phone,
      done: state => state.fastOrder.done,
      orderId: state => state.fastOrder.orderId
    })
  },
  methods: {
    ...mapActions(['fastOrder', 'userName', 'userPhone'])
  }
}
</script>

<style lang="scss" scoped>
.callback {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 38px 73px 40px 67px;
  width: 767px;

  &__heading {
    margin-top: 0;
    margin-bottom: 14px;
    color: #515151;
    font-size: 25px;
    font-weight: 400;
  }

  &__vendor-code, &__description {
    color: #515151;
    font-size: 16px;
  }

  &__description {
    margin-top: 19px;
    margin-bottom: 21px;
    width: 60%;
    white-space: pre-line;
    text-align: center;
  }

  &__field {
    position: relative;
    display: block;
    width: 55.74%;
    margin-bottom: 21px;
    border-radius: 3px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    border: 1px solid #fff;
    background-color: #fff;

    input {
      padding: 5.5px 20px;
      border: none;
      outline: none;
      width: 100%;
      font-size: 16px;
      color: #515151;

      &::placeholder {
        color: #c0c0c0;
        font-size: 16px;
        &::first-letter {
          color: #515151;
        }
      }
    }
  }

  &__button {
    padding: 10px 46px;
    margin-top: 11px;
    border: none;
    outline: none;
    border-radius: 3px;
    background-color: #8fb35b;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
  }
}
</style>
