document.addEventListener('DOMContentLoaded', () => {
  const modalButtons = [...document.querySelectorAll('[data-modal-target]')]
  const backdrop = document.querySelector('.backdrop')
  const closeIcon = [...document.querySelectorAll('.icon-close')]
  const modals = [...document.querySelectorAll('.modal-wrapper')]

  modalButtons.forEach(element => {
    const dataId = element.dataset.modalTarget.replace('#', '')
    const modal = document.getElementById(dataId)
    element.addEventListener('click', (e) => {
      e.preventDefault()
      const target = e.target
      const modalHeight = modal.firstElementChild.offsetHeight
      const browserHeight = document.documentElement.clientHeight

      if (browserHeight <= modalHeight) {
        modal.firstElementChild.style.marginTop = 'auto'
      }

      modal.style.overflowY = 'scroll'
      document.documentElement.style.overflow = 'hidden'

      if (!target.classList.contains('nobackdrop')) {
        backdrop.classList.add('backdrop_opened')
      }

      modal.classList.add('modal-wrapper_opened')
    })

    modal.addEventListener('click', (e) => {
      e.preventDefault()
      const target = e.target
      console.log(target.classList.contains('modal'))
      if (!target.classList.contains('modal')) {
        document.documentElement.style.overflow = null
        backdrop.classList.remove('backdrop_opened')
        modals.forEach(element => element.classList.remove('modal-wrapper_opened'))
      }
    })
  })

  closeIcon.forEach(element => {
    element.addEventListener('click', (e) => {
      e.preventDefault()

      document.documentElement.style.overflow = null
      backdrop.classList.remove('backdrop_opened')
      modals.forEach(element => element.classList.remove('modal-wrapper_opened'))
    })
  })
})
