import cart from '../../api/cart'

const state = {}

const getters = {}

const mutations = {}

const actions = {
  addCart ({ commit }, itemData) {
    return new Promise((resolve, reject) => {
      cart.addToCart((data, html) => {
        commit('setCart', data)
        if (html) {
          resolve(html)
        } else {
          reject(data)
        }
      }, itemData)
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
