<template>
  <div>
    <slot name="visible" />
    <transition name="fade">
      <div v-if="expand">
        <slot name="hidden" />
      </div>
    </transition>

    <a
      href="#"
      class="expand-button"
      @click.prevent="expand = !expand"
    >{{ expand ? textCollapse : textExpand }}</a>
  </div>
</template>

<script>
export default {
  name: 'SeoBlock',
  props: {
    textExpand: {
      type: String,
      default: 'Развернуть текст'
    },
    textCollapse: {
      type: String,
      default: 'Скрыть текст'
    }
  },
  data () {
    return {
      expand: false
    }
  }
}
</script>

<style lang="scss" scoped>
    .seo-text__content {
        position: relative;
        @include width(90%);
        color: #515151;
        font-size: 16px;
        line-height: 25.05px;
        overflow: hidden;

        .expand-button {
            display: block;
            width: 100%;
            padding-left: 30px;
            margin-left: -30px;
            z-index: 3;
            cursor: pointer;
            background: #fff;
            text-align: right;
        }

        @media (max-width: 767px) {
            @include width(100%);
            font-size: 13px;
        }
    }

    .seo-block {
        position: relative;
        margin-bottom: 33px;
        overflow: hidden;
        border-top: 1px solid #d6d6d6;
        border-bottom: 1px solid #d6d6d6;
        transition: all ease 0.3s;

        .expand-button {
            position: absolute;
            bottom: 14px;
            right: 35px;
            color: #7e9d51;
            font-size: 12px;
            text-decoration: underline;
        }

        @media (max-width: 767px) {
            height: 68px;
            margin-bottom: 0;

            .expand-button {
                bottom: 7px;
                right: 15px;
            }
        }

        @media (min-width: 768px) and (max-width: 1024px) {
            margin-bottom: 0;
        }
    }

    .fade-enter-active {
        transition: all ease 1s;
    }

    .fade-leave-active {
        transition: all ease 0.5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
        opacity: 0;
    }
</style>
