<template>
  <a
    class="cart"
    :class="{ cart_mobile: !fullVersion }"
  >
    <i
      v-if="fullVersion"
      class="icon-cart"
    />
    <i
      v-else
      class="icon-shopping-cart"
    />
    <slot v-if="fullVersion" />
    <span :class="{ counter: !fullVersion }">
      {{ fullVersion ? `(${getCount})` : getCount }}
    </span>
  </a>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'Cart',
  data () {
    return {
      fullVersion: true
    }
  },
  computed: {
    ...mapGetters(['getCount'])
  },
  methods: {
    ...mapActions(['setCart'])
  },
  mounted () {
    this.setCart()
    this.$nextTick(function () {
      this.fullVersion = !window.matchMedia('(max-width: 1024px)').matches

      window.addEventListener('resize', () => {
        this.fullVersion = !window.matchMedia('(max-width: 1024px)').matches
      })
    })
  }
}
</script>

<style lang="scss" scoped>
  .cart {
    position: relative;
    padding: 14.5px 0;
    border-radius: 3px;
    color: #fff;
    font-weight: 700;
    background-color: #8fb35b;
    text-align: center;
    cursor: pointer;

    &:hover {
      background-color: #fbc000;
    }

    .icon-cart {
       margin-right: 10px;
    }

    .icon-shopping-cart {
      font-size: 18px;
    }

    &_mobile {
      flex-basis: auto;
      width: auto;
      padding: 0;
      background: transparent;
      color: #000;
      margin-left: 10px;

      @media (max-width: 767px) {
        margin-left: 15px;
        margin-right: 9px;
      }
    }
  }

  .counter {
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #8fb35b;
    font-size: 9px;
    color: #fff;
  }
</style>
